import React, { Component } from 'react';
import cover from '../assets/cover.png';
import { bounceInLeft } from 'react-animations';
import Radium, {StyleRoot} from 'radium';
import { Instagram ,Facebook,Linkedin} from 'react-feather';



class Cover extends Component {
    state = {  }
    render() { 
        const styles={
            coverBackground : {
                backgroundImage:"url("+cover+") ",
                height:700,
                width:"100%",
                backgroundSize: "cover",
                overflow:"hidden"
               },

            coverTitle : {
                padding:100,
                color:"white",
                animation: 'x 5s',
                animationName: Radium.keyframes(bounceInLeft, 'bounce-in-left')
               },

            listStyle:{
                list:{
                    margin:20
                }
            }
        };

        return ( 
            <React.Fragment>
                <div className="row" style={styles.coverBackground}>
                    <div className="col-sm">
                    <ul style={{ listStyleType: "none",position:"fixed",marginTop:200}}>
                        <li style={styles.listStyle.list}><Instagram color="white" size={30}/></li>
                        <li style={styles.listStyle.list}><Facebook color="white" size={30}/></li>
                        <li style={styles.listStyle.list}><Linkedin color="white" size={30}/></li>
                    </ul>
                    </div>
                    <div className="col-sm">
                        <StyleRoot style={styles.coverTitle}>
                            <h1 style={{fontSize:100}}>I'm TJ,</h1>
                            <h1 style={{fontSize:100}}>Full Stack Mobile Developer</h1>
                        </StyleRoot>
                    </div>
                </div>
            </React.Fragment>
         );
    }
}
 
export default Cover;